<template>
  <div class="servers">
    <ul class="servers-list">
      <li v-for="(server, index) in servers" :key="index">
        <span :class="(server.id === current.server) ? 'selected' : ''" @click="setServer(server.id)">{{ server.name }}</span>
        <div v-if="server.id === current.server">
          <ul class="servers-list sessions-list">
              <li v-for="session in sessions" :key="session.id">
                <span :class="(session.id === current.session) ? 'selected' : ''" @click="setSession(session.id)">{{ session.name }}</span>
              </li>
          </ul>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";

export default {
  name: "servers",
  methods: {
    setServer(id) {
      this.$store.dispatch("selectComponent", { type: 0, id });
    },
    setSession(id) {
      console.log('servers', this.serverTree);
      this.$store.dispatch("selectComponent", { type: 1, id });
    },
    setPane(id) {
      this.$store.dispatch("setPane", id);
    }
  },
  computed: {
    ...mapState(["servers", "current", "sessions", "windows", "panes"]),
    ...mapGetters(["serverTree"])
  }
};
</script>

<style scoped>
.selected {
  color: yellow !important;
}

.servers-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  padding-left:0px;
  overflow: hidden;
  background-color: #333333;
}

.servers-list li span {
  display: block;
  color: white;
  text-align: left;
  padding: 8px 16px;
  text-decoration: none;
  border-bottom: 2px solid white;
}

.sessions-list {
  padding-left: 16px;
}

.servers-list li span:hover {
  background-color: #111111;
  cursor: pointer;
}
</style>